.setup-mode-container {
  width: 100%;
  max-width: none;
  height: 12.5rem;
  display: flex;
  flex-direction: column;
}

.mode-tabs {
  display: flex;
  gap: 0;
  margin-bottom: 1.25rem;
  border-bottom: 0.125rem solid #ddd;
}

.tab {
  padding: 0.625rem 1.25rem;
  background: none;
  border: none;
  border-bottom: 0.1875rem solid transparent;
  cursor: pointer;
  font-family: monospace;
  font-size: 0.875rem;
  font-weight: 500;
  color: #666;
  transition: all 0.2s;
  margin-bottom: -0.125rem;
}

.tab:hover {
  color: #333;
  background-color: rgba(0, 0, 0, 0.03);
}

.tab.active {
  color: #007bff;
  border-bottom-color: #007bff;
  font-weight: bold;
}

.tab.light {
  color: #666;
}

.tab.light.active {
  color: #007bff;
}

.tab.cyborg {
  color: #80ffdb;
}
.tab.vapor {
  color: #ff6baf;
}
.tab.solar {
  color: #b58900;
}
.tab.superhero {
  color: #df691a;
}

.tab.cyborg.active {
  color: #80ffdb;
  border-bottom-color: #80ffdb;
}
.tab.vapor.active {
  color: #ff6baf;
  border-bottom-color: #ff6baf;
}
.tab.solar.active {
  color: #b58900;
  border-bottom-color: #b58900;
}
.tab.superhero.active {
  color: #df691a;
  border-bottom-color: #df691a;
}

.tab.blue {
  color: #1976d2;
}

.tab.blue.active {
  color: #1565c0;
  border-bottom-color: #1565c0;
}

.analysis-controls {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
  flex: 1;
  height: 6.875rem;
  justify-content: flex-start;
}

.play-controls {
  display: flex;
  flex-direction: row;
  gap: 0.75rem;
  flex: 1;
  height: 6.875rem;
  align-items: flex-start;
  flex-wrap: wrap;
}

.auth-required {
  padding: 0.9375rem;
  background-color: rgba(0, 0, 0, 0.02);
  border-radius: 0.3125rem;
  text-align: center;
  min-height: 3.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.auth-required p {
  margin: 0;
  color: #856404;
  font-family: monospace;
}

.play-status-row {
  display: flex;
  flex-direction: row;
  gap: 0.75rem;
  padding: 0.625rem;
  background-color: rgba(0, 0, 0, 0.02);
  border-radius: 0.3125rem;
  flex: 1;
  align-items: flex-start;
  flex-wrap: wrap;
}

.status-stack {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  flex: 1;
  min-width: 15.625rem;
}

.user-info {
  display: flex;
  flex-direction: column;
  gap: 0.375rem;
  padding: 0.5rem 0.75rem;
  background-color: rgba(76, 175, 80, 0.1);
  border-radius: 0.3125rem;
  font-family: monospace;
  font-size: 0.8125rem;
  flex: 0 0 auto;
  min-width: 13.75rem;
  max-width: 17.5rem;
}

.quick-pairing {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  background-color: rgba(0, 0, 0, 0.03);
  border-radius: 0.3125rem;
  flex: 1;
  min-width: 18.75rem;
}

.quick-pairing-row {
  display: flex;
  align-items: center;
  gap: 0.9375rem;
  padding: 0.75rem;
  background-color: rgba(0, 0, 0, 0.03);
  border-radius: 0.3125rem;
  flex-wrap: wrap;
}

.quick-pairing-label {
  font-family: monospace;
  font-size: 0.875rem;
  font-weight: bold;
  white-space: nowrap;
}

.time-control-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  flex: 1;
}

.time-control-btn {
  padding: 0.5rem 0.75rem;
  font-family: monospace;
  font-size: 0.8125rem;
  font-weight: bold;
  border: none;
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
}

.time-control-btn:hover:not(:disabled) {
  transform: translateY(-0.125rem);
  box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.2);
}

.time-control-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Bootstrap theme colors */
.btn-info {
  background-color: #17a2b8;
  color: white;
}

.btn-info:hover:not(:disabled) {
  background-color: #138496;
}

.btn-primary {
  background-color: #007bff;
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background-color: #0056b3;
}

.btn-danger {
  background-color: #dc3545;
  color: white;
}

.btn-danger:hover:not(:disabled) {
  background-color: #c82333;
}

