/* Game result styles */
.game-result-compact {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.5rem 0.75rem;
  background-color: rgba(103, 58, 183, 0.15);
  border: 0.0625rem solid #673ab7;
  border-radius: 0.3125rem;
  font-family: monospace;
  font-size: 0.8125rem;
