  min-height: 2.5rem;
}

.result-icon {
  font-size: 1rem;
}

.result-outcome {
  font-weight: bold;
  font-size: 0.875rem;
}

.result-outcome.win {
  color: #388e3c;
}

.result-outcome.loss {
  color: #d32f2f;
}

.result-outcome.draw {
  color: #f57c00;
}

.result-reason {
  color: #666;
  font-style: italic;
  font-size: 0.75rem;
}

.btn-success {
  background-color: #28a745;
  color: white;
  padding: 0.75rem 1.25rem;
  font-family: monospace;
  font-size: 0.875rem;
  font-weight: bold;
  border: none;
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-sm {
