/* Inline game notification styles */
.game-notification {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.625rem 0.75rem;
  margin-top: 0.625rem;
  border-radius: 0.3125rem;
  font-family: monospace;
  font-size: 0.8125rem;
  border: 0.0625rem solid;
  background: rgba(255, 255, 255, 0.1);
}

.notification-icon {
  font-size: 1rem;
  flex-shrink: 0;
}

.notification-text {
  flex: 1;
  font-weight: bold;
}

.notification-close {
  background: none;
  border: none;
  font-size: 1.125rem;
  color: inherit;
  cursor: pointer;
  padding: 0;
  width: 1.25rem;
  height: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.1875rem;
  opacity: 0.7;
  transition: all 0.2s;
}

.notification-close:hover {
  opacity: 1;
  background: rgba(0, 0, 0, 0.1);
}

/* Type-specific notification styles */
.game-notification-error {
  border-color: #f56565;
  background: rgba(245, 101, 101, 0.1);
  color: #c53030;
}

.game-notification-warning {
  border-color: #ed8936;
  background: rgba(237, 137, 54, 0.1);
  color: #c05621;
}

.game-notification-success {
  border-color: #48bb78;
  background: rgba(72, 187, 120, 0.1);
  color: #22543d;
}

.game-notification-info {
  border-color: #4299e1;
  background: rgba(66, 153, 225, 0.1);
  color: #2c5282;
}

